
// This component grid has all the other components of the page in sequence

import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AdvertisementsCarousel from './AdvertisementsCarousel';
import BestOffersCarousel from './BestOffersCarousel';
import RestaurantsNearYouCarousel from './RestauranrsNearYouCarousel';
import TopPicksSingleLineImageList from './TopPicksImageList';
import SuperSaversCarousel from './SuperSaversCarousel';
import CenteredTabs from '../FinalBookPage/Tabs';
import Hidden from '@material-ui/core/Hidden';
import Main from '../../Assets/Main.jpg';
import DineoutLogo from '../../Assets/DineoutLogo.jpg'
import Typography from '@material-ui/core/Typography';
import FoodButtons from './FoodButtons';
import Button from '@material-ui/core/Button';
import Hotel1 from './Hotel1';
import Hotel2 from './Hotel2';
import Hotel3 from './Hotel3';
import Hotel4 from './Hotel4';
import Hotel5 from './Hotel5';
import Hotel6 from './Hotel6';
import EXplore from './Exploore';




const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 0,
    fontFamily: 'Futura',
    color: 'Grey',
  },


  text: {
    fontFamily: 'Futura',
    fontSize: 30,
    color: 'grey',
    padding: 6,
  },

  text2: {
    fontFamily: 'Futura',
    fontSize: 20,
    color: 'grey',
    paddingTop: 4,
    padding: 25,
  },


}));

export default function HomeGrid() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={1} justifyContent="center"

        alignItems="center"
        justify="center"
        style={{ minHeight: '100vh' }}
        direction="column"
      >
        {/* Desktop Components */}

        <Hidden mdDown>
          {/* the tabs */}
          <CenteredTabs />
        </Hidden>

        <Hidden mdDown>
          {/* the main image */}
          <img src={Main} alt="" width="1212" height="650" borderRadius="15">
          </img>
        </Hidden>

        <Hidden mdDown>
          {/* dineout logo */}
          <img src={DineoutLogo} alt="" width="400" height="110">
          </img>
        </Hidden>

        <Hidden mdDown>
          <Typography className={classes.text}>
            What are you waiting for? </Typography>
        </Hidden>

        <Hidden mdDown>
          <Typography className={classes.text2}> Check out your food new paradise ! </Typography>
        </Hidden>

        <Hidden mdDown>
          <FoodButtons />
        </Hidden>

        <Hidden mdDown>
          <Button> Restuarants Near you </Button>
        </Hidden>

      </Grid>



      {/* MObile Components */}

      {/* this is the carousal for advertisements, component name is caorusal1 */}
      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          <AdvertisementsCarousel />
        </Grid>
      </Hidden>

      {/* this is the carousal for best offers, component name is caorusal2 */}
      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          Best Offers
        </Grid>
      </Hidden>

      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          <BestOffersCarousel />
        </Grid>
      </Hidden>

      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          Restaurants Near You
        </Grid>
      </Hidden>

      <Hidden mdUp>
        {/* this is the carousal for restaurants near you, component name is caorusal3 */}
        <Grid item xs={12} md={12}>
          <RestaurantsNearYouCarousel />
        </Grid>
      </Hidden>

      <Hidden mdUp>
        {/* this is the swippable image list for top picks, component name is singleimagelist */}
        <Grid item xs={12} md={12}>
          Top Picks
        </Grid>
      </Hidden>

      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          <TopPicksSingleLineImageList />
        </Grid>
      </Hidden>

      <Hidden mdUp>
        {/* this is the carousal for super savers, component name is caorusal4 */}
        <Grid item xs={12} md={12}>
          Super Savers
        </Grid>
      </Hidden>

      <Hidden mdUp>
        <Grid item xs={12} md={12}>
          <SuperSaversCarousel />
        </Grid>
      </Hidden>





      {/* this grid has been added separately for the retaurants crads, below in the desktop */}
      
      
      <Grid container spacing={0.5} justifyContent="center">
        <Hidden mdDown>
          <Grid item md={4}>
            <Hotel1 />
          </Grid>
        </Hidden>

        <Hidden mdDown>
          <Grid item md={4}>
            <Hotel2 />
          </Grid>
        </Hidden>

        <Hidden mdDown>
          <Grid item md={4}>
            <Hotel3 />
          </Grid>
        </Hidden>

        <Hidden mdDown>
          <Grid item md={4}>
            <Hotel4 />
          </Grid>
        </Hidden>

        <Hidden mdDown>
          <Grid item md={4}>
            <Hotel5 />
          </Grid>
        </Hidden>

        <Hidden mdDown> <Grid item md={4}>
          <Hotel6 />
        </Grid>
        </Hidden>

        <Hidden mdDown>
          <EXplore />
        </Hidden>

      </Grid>
    </div>
  );
}
